*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}
.background {
  background-size: 100%;
  height: 100%;
  width: 100%;
  opacity: 0.2;
  background-position: center center;
  background: cover;
  background: no-repeat;
  z-index: -100;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}
.title {
  font-size: 50px;
  color: rgb(0, 0, 0);
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
  margin-top: 30px;
}
.footer {
  margin-bottom: 16px;
}
.msg {
  text-align: center;
  font-weight: 600;
}

@media (max-width: 1024px) {
  .title {
    font-size: 24px;
  }
  .background {
    background-position: center center;
  }
}
@media (min-width: 1900px) {
  .title {
    font-size: 70px;
  }
  .background {
    background: repeat;
  }
}
